.content{
  display: flex;
  /* gap: 2rem; */
  justify-content: center;
  flex-wrap: wrap;
  font-size: 1.2rem; 
}
.item{
  position: relative;
  padding: .5rem 1rem;
  cursor: pointer;
  background-color: transparent;
}
.item:hover p{
  transition: .25s;
  color: white;
}

@media(max-width: 550px){
  .content{
    display: none;
  }
}