.wrapper{
  position: relative;
  width: 100%;
  height: 3rem;
  background-color: rgb(255, 255, 255);
  z-index: 2;
  border-radius: .5rem;
}
.searchInput{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  z-index: 1;
  padding: 0 4rem 0 .5rem;
  font-size: 1.8rem;
  text-align: center;
}
.searchButton{
  position: absolute;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: .5rem;
  background-color: var(--buttonsCollor);
  z-index: 2;
  right: .5rem;
  top: 50%;
  transform: translateY(-50%);
}
.svgImg{
  position: absolute;
  width: 70%;
  height: 70%;
  top: 50%;
  left: 50%;
  fill: rgb(255, 255, 255);
  transform: translateX(-50%) translateY(-50%);
}

@media(max-width: 650px){
  .searchInput{
    font-size: 1.5rem;
  }
}