.app{
  display: flex;
  align-items: center;
  padding: .5rem;
  border-radius: .5rem;
  background-color: rgb(89, 89, 89);
  gap: .5rem;
  min-width: 19rem;
  cursor: pointer;
}

.app:hover{
  background-color: rgb(143, 143, 143);
  color: black;
}
.app:hover .iconLogo{
  fill:rgb(60, 73, 59);
}
.app:hover .subtitle,.app:hover .title {
  color: black;
}
.text{
  /* display: flex; */
  flex-direction: column;
}
.subtitle{
  color: white;
}
.title{
  font-size: 1.5rem;
  white-space: nowrap;
  color: white;
}
.iconLogo{
  flex: 0 0 3rem;
  width: 3rem;
  height: 3rem;
  fill:white;
}

@media(max-width:550px){
  .app{
    min-width: 18rem;
  }
  .title{
    font-size: 1.2rem;
  }
  .iconLogo{
    width: 2.2rem;
    height: 2.2rem;
  }
}
