.containerGreed{
  position: relative;
  padding: .5rem;
  top:50%;
  max-width: var(--maxWidth);
  margin: 0 auto;
  gap: 1rem;
  display: flex;
  align-items: center;
  transform: translateY(-50%);
}
.burgerContainer{

}
.searchContainer{
  flex-grow: 1;
  width: 22rem;
}
.heartContainer{

}


.containerBack{
  position: absolute;
  bottom: -3rem;
  left: 50%;
  width: 100%;
  background-color: rgb(255, 255, 255);
  max-width: var(--maxWidth);
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: left;
  transform: translateX(-50%);
}
