%template {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 1.2rem;
  height: 100%;
  background: inherit;
  border-radius: 2px;
  transform-origin: left center;
}

.wrapper{
  position: relative;
  width: 6rem;
  height: 2rem;
  cursor: pointer;
  // border: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:hover{
    transform: scale(.9);
  }
}

.containerLine{
  position: relative;
  top:0;
  left: 0;
  width: 50%;
  height: 100%;
}

.line{
  position: absolute;
  top:50%;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: red;
  transform: translateY(-50%);
  border-radius: 2px;
  &::before{
    @extend %template;
    transform: rotate(20deg);
  }
  &::after{
    @extend %template;
    transform: rotate(-20deg);
  }
}

.inscript{
  font-size: 1.2rem;
  color: red;
  font-weight: bold;
}

.link{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
}